@charset "UTF-8";
html::before {
  content: "";
  display: block;
  position: absolute;
  z-index: -1001;
  width: 15em;
  height: 8em;
  top: 0;
  left: 0;
  background: url("/assets/images/shapes.svg") no-repeat;
  background-size: cover;
  transform: rotate(0.5turn);
}

footer::after {
  content: "";
  display: block;
  position: absolute;
  z-index: -1001;
  width: 15em;
  height: 8em;
  bottom: 0;
  right: 0;
  background: url("/assets/images/shapes.svg") no-repeat;
  background-size: cover;
}

h1 {
  max-width: 5em;
  margin-left: auto;
  margin-right: auto;
}

h1::after {
  content: "";
  display: block;
  position: absolute;
  z-index: -1000;
  width: 3.6em;
  height: 0.5em;
  margin-left: calc(50% - 1.8em);
  bottom: 0.05em;
  background-color: var(--color-3);
}

main {
  animation: fadeIn;
  animation-duration: 1s;
}

footer {
  height: 13em;
}
footer section {
  margin-left: 5em;
  margin-right: 5em;
}

.projects-list {
  list-style-type: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
}

#design-projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  row-gap: 4em;
  -moz-column-gap: 4em;
       column-gap: 4em;
}

#design-projects figure {
  margin: 0;
  position: relative;
}

#design-projects figcaption {
  color: var(--color-1);
  background-color: var(--color-2);
  padding: 1em 1.5em;
  font-weight: normal;
  position: absolute;
  bottom: -1em;
  left: -2em;
  text-align: left;
}

#design-projects img {
  max-width: 12em;
}

#design-projects a:hover figcaption, #design-projects a:focus figcaption {
  background-color: var(--color-3);
}
#design-projects a:hover img, #design-projects a:focus img {
  outline: 0.4em solid var(--color-3);
}

#snippets-projects, #art-projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  row-gap: 2em;
  -moz-column-gap: 2em;
       column-gap: 2em;
}
#snippets-projects figure, #art-projects figure {
  margin-bottom: 0;
}
#snippets-projects img, #art-projects img {
  border-radius: 50%;
  max-width: 5em;
}
#snippets-projects a:hover img, #snippets-projects a:focus img, #art-projects a:hover img, #art-projects a:focus img {
  outline: 0.2em solid var(--color-3);
}

#translation-projects {
  line-height: 1.8;
  text-align: center;
}

#translation-projects li {
  display: inline;
}

#translation-projects li + li::before {
  display: inline-block;
  margin: 0.5em 0.5em 0 0.3em;
  width: 0.7em;
  height: 0.7em;
  border-radius: 50%;
  content: "";
  background-color: var(--color-3);
}

#translation-projects a {
  text-decoration: none;
}

#translation-projects a:hover {
  text-decoration: underline;
}

#socials {
  font-family: var(--font-stack-accent);
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-size: 0.7em;
}
#socials a {
  text-decoration: none;
}
#socials ul {
  list-style: none;
  padding-left: 0;
}
#socials li {
  display: inline-block;
}
